import React from "react";
import TopBanner from '../../components/TopBanner'
import Preview from '../../components/Preview'
import Helmet from "react-helmet";

export default function Injury({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div className="preview-feed">
    <Helmet title={`Самые безопасные упражнения`} />
    <TopBanner style={injuryStyle} title={'Самые безопасные упражнения'} />
      {posts
        .filter(post => post.node.frontmatter.injury <= '5')
        .map(({ node: post }) => {
          return (
            <Preview post={post} />
          );
        })}
    </div>
  );
}

const injuryStyle = {
  backgroundImage: 'linear-gradient(to right top, #944444, #b13e3d, #cc3534, #e62726, #ff1010)'
};

export const pageQuery = graphql`
  query InjuryQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 180)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            image
            image_2
            image_3
            image_4
            filterby
            efficiency
            injury
            bodypart
            filtertype
            type
            filterinventory
            inventory
          }
        }
      }
    }
  }
`;



